body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-size: 15px;
  line-height: 30px;
  font-family: @font;
  font-weight: 400;
  color: @gray-dark;
  font-style: normal;

  background-color: @white;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden:not(:focus),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  border: 0;
  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
